<template>
	<dt-scroll-box>
		<div slot="top">
			<p class="dt-bread-item">项目批复</p>
		</div>
		<div slot="main-content">
			<!-- 项目申报-项目查询界面-->
			<div class="projectQuery">
				<!-- 中间下拉选择区域 -->
				<div class="qmiddle">
					<ul>
						<li class="fl">
							<!-- 竖排1-1 -->
							<div class="proNum">
								<span class="fl">项目名称：</span>
								<el-input placeholder="请输入内容" v-model="value1">

								</el-input>
							</div>
							<div class="name">
								<span class="fl">姓名：</span>
								<div class="selectInp">
									<el-input placeholder="请输入内容" v-model="value1">

									</el-input>
								</div>
							</div>


							<!-- 1-3 -->

						</li>
						<li class="fl">
							<div class="proType">
								<span class="fl">项目类型：</span>
								<div class="selectInp">
									<el-select v-model="value3" clearable placeholder="请选择">
										<el-option v-for="item in proTypeList" :key="item.value" :label="item.label" :value="item.value"></el-option>
									</el-select>
								</div>
							</div>
							<!-- 2-2 -->
							<div class="supportUnit">
								<span class="fl">依托单位：</span>
								<div class="selectInp">
									<el-input placeholder="请输入内容" v-model="value1">

									</el-input>
								</div>
							</div>

						</li>
						<li class="fl">
							<!-- 1-2 -->
							<div class="disciplineClass">
								<span class="fl">学科分类：</span>
								<div class="selectInp">
									<el-select v-model="value2" clearable placeholder="请选择">
										<el-option v-for="item in subjectCategoryList" :key="item.value" :label="item.label" :value="item.value"></el-option>
									</el-select>
								</div>
							</div>


						</li>
						<li class="fr">
							<div class="reset fr">
								<el-button class="dt-btn" @click="approvalProject">
									<i class="iconfont icon-shanchu"></i>批复
								</el-button>
							</div>
						</li>
					</ul>
				</div>
				<!-- 表格部分 -->
				<!-- 表格 -->
				<el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange"
				 border :header-cell-style="{background:'#EBEBEB'}">
					<el-table-column type="selection" width="55"></el-table-column>
					<el-table-column prop="projectName" label="项目名称"></el-table-column>
					<el-table-column prop="name" label="姓名"></el-table-column>
					<el-table-column prop="dependDeptName" label="依托单位"></el-table-column>
					<el-table-column prop="proType" label="项目类型" :formatter="proTypeFormat"></el-table-column>
					<el-table-column prop="applyNum" label="申报编码"></el-table-column>
					<el-table-column prop="subjectCategory" label="学科方向" :formatter="subjectCategoryFormat"></el-table-column>
					<el-table-column prop="proStat" label="项目状态" :formatter="proStatFormat"></el-table-column>

				</el-table>

			</div>
		</div>
	</dt-scroll-box>
</template>
<script>
	/*
	 *项目申报-项目查询界面
	 * @author renchao 2019-02-13
	 */
	export default {
		data() {
			return {
				// 下拉选择区域
				year: [{
					id: "2017",
					label: "2017"
				}, {
					id: "2018",
					label: "2018"
				}, {
					id: "2019",
					label: "2019"
				}, {
					id: "2020",
					label: "2020"
				}],
				subjectCategoryList: [],
				proStatList: [],
				proTypeList: [],
				value1: "",
				value2: "",
				value3: "",
				value4: "",
				value5: "",
				value6: "",
				value7: "",
				value8: "",
				value9: "",
				value10: "",
				// 分页
				currentPage: 1,
				// 搜索框
				//   search1: "",
				//   search2: "",
				// 表格
				tableData: [],
				multipleSelection: [],
				// 绑定表单数据
				formData: {
					username: "",
					password: "",
					email: "",
					mobile: ""
				},
				// 表单验证规则
				formRules: {
					username: [{
							required: true,
							message: "请输入批准号",
							trigger: "blur"
						},
						{
							min: 1,
							max: 5,
							message: "长度在 1 到 5 个字符",
							trigger: "blur"
						}
					],
					password: [{
							required: true,
							message: "请输入活动名称",
							trigger: "blur"
						},
						{
							min: 1,
							max: 5,
							message: "长度在 1 到 5 个字符",
							trigger: "blur"
						}
					]
				},
				editUserDialogFormVisible: false
			};
		},

		created() {
			this.$dt.getAjax(
				'/project/list/approvalProject', {},
				(res) => {

					this.tableData = res;
				},
				() => {

				}
			)
		},
		methods: {

			//  跳转对应申请界面
			handleApply(row) {
				this.$router.push({
					name: row.routeName
				});
				// console.log(row);
			},
			proTypeFormat(row, column) {

				if (row.subjectCategory) {
					return "111";
				}
			},
			subjectCategoryFormat(row, column) {

				for (var i = 0; i < this.subjectCategoryList.length; i++) {
					if (row.subjectCategory == this.subjectCategoryList[i].value) {
						return this.subjectCategoryList[i].label;
					}
				}
			},
			proStatFormat(row, column) {

				if (row.subjectCategory) {
					return "111";
				}
			},

			approvalProject() {
				alert(this.multipleSelection);

				this.$dt.postAjax('/project/approval', this.multipleSelection,
					(res) => {
						this.$dt.getAjax(
							'/project/list/approvalProject', {},
							(res) => {

								this.tableData = res;
							},
							() => {

							}
						)
					},
					() => {

					});

				// 				this.$dt.getAjax('/sys-dict/codes/SRCAT+PROST', {}, function(data) {
				// 					alert(data)
				// 				}, function(err) {
				// 					alert(err)
				// 				});
				// 			let newRow = {}
				// 			let newRowShow = []
				// 			TABLEKEYS.forEach((key)=>{
				// 				newRow[key] = ''
				// 				newRowShow.push(false)
				// 			})
				// 			// 添加索引
				// 			newRow['index'] = this.tableData.length
				// 			this.tableInputShow.push(newRowShow)
				// 			this.tableData.push(newRow)
			},

			// 确认编辑按钮
			SureHandleEdit() {},
			// 删除委员信息
			handleDelete() {
				this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
						confirmButtonText: "确定",
						cancelButtonText: "取消",
						type: "warning",
						center: true
					})
					.then(() => {
						this.$message({
							type: "success",
							message: "删除成功!"
						});
					})
					.catch(() => {
						this.$message({
							type: "info",
							message: "已取消删除"
						});
					});
			},
			// 关闭按钮
			handleClosed() {
				for (const key in this.formData) {
					this.formData[key] = "";
				}
			},
			// 表格
			toggleSelection(rows) {
				if (rows) {
					rows.forEach(row => {
						this.$refs.multipleTable.toggleRowSelection(row);
					});
				} else {
					this.$refs.multipleTable.clearSelection();
				}
			},
			handleSelectionChange(val) {
				this.multipleSelection = val;
			},
			// 分页
			handleSizeChange(val) {
				console.log(`每页 ${val} 条`);
			},
			handleCurrentChange(val) {
				console.log(`当前页: ${val}`);
			}
		}
	};
</script>
<style lang="less">
	.projectQuery {
		padding-bottom: 60px;
	}

	// 小屏
	@media screen and (max-width: 1600px) {
		.qmiddle>ul>li:nth-child(1) span.fl {
			width: 100%;
		}

		.qmiddle>ul>li:nth-child(2) span.fl {
			width: 100%;
		}

		.qmiddle>ul>li:nth-child(3) span.fl {
			width: 100%;
		}

		.qmiddle>ul>li:nth-child(-n + 3) {
			width: 25%;

			// 下拉前文字
			span {
				line-height: 40px;
				font-size: 14px;
				text-align: left;
			}
		}
	}

	// 大屏
	@media screen and (min-width: 1601px) {
		.qmiddle>ul>li:nth-child(1) span.fl {
			width: 30%;
		}

		.qmiddle>ul>li:nth-child(3) span.fl {
			width: 32%;
		}

		.qmiddle>ul>li:nth-child(-n + 3) {
			width: 27%;

			// 下拉前文字
			span {
				line-height: 40px;
				font-size: 14px;
				text-align: right;
			}
		}
	}

	// 查询按钮
	// 重置按钮
	.el-button--primary.is-plain {
		background: #fff;
	}

	.query .el-button,
	.reset .el-button {
		//   height: 40px;
		//   line-height: 40px;
		//   padding: 0 20px;
		//   background-color: #63b4ff;
		border: 1px solid #63b4ff;
	}

	.reset .el-button {
		margin-right: 30px;
		margin-left: 20px;
	}

	.query .el-icon-search,
	.reset .el-icon-refresh {
		color: #63b4ff;
	}

	.query .el-button:hover .el-icon-search,
	.reset .el-button:hover .el-icon-refresh {
		color: #fff;
	}

	// 文字  "项目查询"
	.txtquery {
		font-size: 16px;
		font-weight: 700;
		padding: 10px;
	}

	// 查询页中部
	.qmiddle {
		overflow: hidden;
		margin-bottom: 50px;
		padding: 10px;
	}

	// .qmiddle > ul > li:nth-child(-n + 3) {
	//   width: 25%;
	//   // 下拉前文字
	//   span {
	//     line-height: 40px;
	//     font-size: 14px;
	//     text-align: right;
	//   }
	// }
	// .qmiddle > ul > li:nth-child(1) span {
	//   width: 25%;
	// }
	// .qmiddle > ul > li:nth-child(3) span {
	//   width: 30%;
	// }
	.qmiddle>ul>li>div {
		margin-top: 20px;
	}

	// 下拉框
	.el-input__inner {
		//   width: 90%;
	}

	.el-input {
		max-width: 190px;
	}

	.el-input__suffix {
		//   right: 14px;
	}

	// 表格部分
	// 分页
	.paging {
		margin-top: 20px;
		margin-left: 20%;
	}
</style>



